<template>
  <div class="box1">
    <el-card class="box-card">
      <h3>菜品点评 |</h3>
      <el-divider>
        <i class="el-icon-eleme"></i>
      </el-divider>
      <!-- <textarea rows="5" cols="30" v-model="obj.comment"></textarea> -->
      <div class="put">
      <el-input type="textarea" :rows="10" placeholder="请输入内容" v-model="obj.comment"></el-input>
       </div>
      <button @click="btnSubmit" class="btn1">提交评论</button>
      <router-link to="/seasonalmenu/autumnpage" tag="button">返回</router-link>
    </el-card>
  </div>
</template>

<script>
import { request } from "../../../network/request";
export default {
  name: "AutumnComment",
  data() {
    return {
      obj: {
        comment: "",
        menuId: this.$route.query.id
      }
    };
  },
  methods: {
    btnSubmit() {
      if (localStorage.getItem("token")) {
        const jon = JSON.stringify(this.obj);
        request({
          url: "/autumnComment",
          method: "post",
          data: jon,
          headers: { "Content-Type": "application/json;charset=UTF-8" }
        })
          .then(res => {
            console.log(res.data.message);
            alert("评论成功");
            this.$router.push({
              path: "/autumnmenucontent",
              query: { id: this.obj.menuId }
            });
          })
          .catch(err => {
            console.log(err);
          });
      } else {
        this.$router.push("/login");
      }
    }
  }
};
</script>

<style scoped>
.box1 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.btn1 {
  margin-left: 108px;
  margin-right: 10px;
}
button {
  border: 2px solid #f6652c;
  background-color: #f6652c;
  height: 30px;
  width: 70px;
  color: #fff;
  font-size: 14px;
  border-radius: 5px;
}
.box-card {
  width: 980px;
}
.put {
  margin-bottom: 26px;
}
</style>